import React, { useState } from 'react';

import NewsEvent from './NewsEvent';

const MoreContent = () => {
    return (
        <>
            <h6 className="mt-2">Mn hnh gp 7,6 inch ln nht th gii smartphone</h6>
            <img className="w-100" src="https://media.doanhnghiepvn.vn/Images/Uploaded/Share/2021/05/05/6e1iphone-12-series.jpg" alt="" />
            <p>Mn hnh gp Galaxy Z Fold 2 5G khi m ra c kch thc ln ti 7,6 inch v c lm vin mng c 4 cnh tuyt p. Bn s c tn hng mn hnh ln cha tng thy trn th gii smartphone, d l c bo, vo mng x hi, xem phim hay chi game, tt c u cho nhng tri nghim th v.</p>
        </>
    );
};

function ProductDesc(props) {
    const [isShow, setShow] = useState(false);
    return (
        <>
            <h6 className="mt-4 mb-2 m-0">M T SN PHM</h6>
            <div className="description row bg-white p-3 m-1">
                <div className="description--product col-md-8 p-0 pr-2">
                    <p>Khng ging bt k chic in thoi no khc, Samsung Galaxy Z Fold 2 5G s hu mn hnh gp k diu m mi khi m mn hnh, bn  m ra tng lai ca ngnh cng nghip smartphone.</p>
                    <img className="w-100" src="https://images.fpt.shop/unsafe/fit-in/filters:quality(90)/fptshop.com.vn/Uploads/images/2015/Tin-Tuc/QuanLNH2/samsung-galaxy-zfold2-6.jpg" alt="" />

                    {isShow === true ? <MoreContent /> : ''}
                    {isShow === false ?
                        <div className="text-center ShowMore">
                            <button type="button" onClick={() => { setShow(true); }} className="btn btn-primary btn-sm mt-3">c thm
                                <i className="fas fa-caret-down ml-2" />
                            </button>
                        </div>
                        : ''}
                </div>
                <NewsEvent />
            </div>
        </>
    );
}

export default ProductDesc;