import React from 'react';
import { Tab, TabList, TabPanel, Tabs } from 'react-tabs';

function OptionProd(props) {
    return (
        <>
            <Tabs>
                <TabPanel><small>Chn mu:</small> <strong className="text-primary">Trng</strong> </TabPanel>
                <TabPanel><small>Chn mu:</small> <strong className="" style={{ color: 'orange' }}>Vng</strong> </TabPanel>
                <TabPanel><small>Chn mu:</small> <strong className="text-secondary">Xm</strong> </TabPanel>

                <TabList>
                    <Tab>Trng</Tab>
                    <Tab>Vng</Tab>
                    <Tab>Xm</Tab>
                </TabList>
            </Tabs>
        </>
    );
}

export default OptionProd;